import React from 'react';
import './Details.css'
import data from '../wad-data.js';
import { Link } from 'react-router-dom';
import importedImages from '../importedImages'

function Details(props) {
  const { id } = props.match.params // Location index
  const { image, title, desc, credit, captions, citation, website } = data[id]
  return (
    <div className="Details">
      <div className="Details-image">
        <Link to={`/`}>
          <div className="container">
            <img className="image" src={importedImages[id]} alt={title} />
            <div className="bottom-right">{captions[0]}</div>
            {/* <div className="center">{id}</div> */}
          </div>
        </Link>
      </div>
      <div className="Details-info">
        <h1 className="Details-title">{ title }</h1>
        <p className="Details-credit">  { credit }</p><br/>
        <p className="Details-desc">{ desc }</p><br/>
        <p className="Details-citation">{ citation }</p><br/><br/>
        <a href={website} target="_blank">More Information</a> 
      </div>
    </div>
  )
}
 
export default Details;