import React from 'react'

function About() {
  return (
    <div>
      <h1>About gems by wad</h1>
      <p>Gems by wad is an interactive world map showcasing various environmental art projects and
 structures. WAD hand-picked these entries based on historical significance, elegant design,
 and current trends. Often, we do not pay attention to these projects’ sites and their
 surroundings, so this website can provide some aspect of what those areas may be. Our goal is
 to share our excitement for these projects and let us discover and explore new ideas that will
 enrich our lives.
      </p>
    </div>
  )
}

export default About